.heading {
  position: relative;
  line-height: 60px;
  font-weight: 600;
  display: inline-block;
  width: 750px;
}
.frame43 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.content3,
.frame42,
.frame44 {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.content3 {
  align-items: flex-start;
  gap: var(--gap-5xl);
}
.frame42,
.frame44 {
  align-items: center;
  font-size: var(--font-size-27xl);
}
.frame42 {
  background-color: var(--color-white);
  flex-direction: column;
  padding: var(--padding-45xl) var(--padding-137xl);
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--font-size-33xl);
  color: var(--color-gray-200);
  font-family: var(--font-poppins);
}
