.number,
.supportingText,
.text {
  align-self: stretch;
  position: relative;
}
.number {
  letter-spacing: -0.02em;
  line-height: 50px;
  font-weight: 600;
}
.supportingText,
.text {
  line-height: 28px;
}
.supportingText {
  font-size: var(--font-size-base);
  line-height: 24px;
}
.textAndSupportingText {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  font-size: var(--font-size-5xl);
}
.numberAndText {
  width: 264px;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-base);
}
.metricItem,
.numberAndText,
.row {
  display: flex;
  justify-content: flex-start;
}
.metricItem {
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
  box-shadow: 4px 4px 7px rgba(18, 18, 18, 0.15);
  flex-direction: column;
  padding: var(--padding-xs) var(--padding-0);
  align-items: center;
}
.row {
  flex-direction: row;
  align-items: flex-start;
  gap: var(--gap-5xl);
  text-align: center;
  font-size: var(--font-size-27xl);
  color: var(--color-gray-200);
  font-family: var(--font-poppins);
}
